<template>
  <div class="videoinput-host">
    <div v-if="ie11" class="videoinput">
      <object ref="ocx" classid="clsid:30516390-004f-40b9-9fc6-c9096b59262e" />
    </div>
    <div v-else class="videoinput" :style="containerStyle">
      <video ref="video" playsInline />
      <canvas ref="canvas" />
      <div class="mask-host">
        <MaskVue
          ref="maskVue"
          @create="onMask($event)"
          @delete="onMask($event)"
          @modify="onMask($event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import MaskVue from './mask.vue';

/**
 * @typedef {import('./typedef.js').VideoDevice} VideoDevice
 * @typedef {import('./typedef.js').VideoResolution} VideoResolution
 * @typedef {import('./typedef.js').VideoSettings} VideoSettings
 * @typedef {import('./typedef.js').WorkerCommand} WorkerCommand
 * @typedef {import('./typedef.js').DoProcessRequest} DoProcessRequest
 * @typedef {import('./typedef.js').DoConvertRequest} DoConvertRequest
 * @typedef {import('./typedef.js').SetMaskRequest} SetMaskRequest
 */

export default {
  components: {
    MaskVue,
  },

  data() {
    return {
      ie11: false,
      devices: /** @type {VideoDevice[]} */ ([]),
      device: /** @type {VideoDevice} */ (null),
      resolutions: /** @type {VideoResolution[]} */ ([]),
      resolution: /** @type {VideoResolution} */ (null),
      stream: /** @type {MediaStream} */ (null),
      framerate: 1,
      settings: /** @type {VideoSettings} */ ({
        rotateAngle: 0,
        colorMode: 0,
        jpegQuality: 50,
      }),
      containerStyle: {},
      resizeObserver: /** @type {ResizeObserver} */ (null),
      worker: /** @type {Worker} */ (null),
      workerSerial: 0,
      workerQueue: {},
    };
  },

  created() {
    // 是否為IE11
    this.ie11 = !navigator.mediaDevices;

    // 設定值變更
    if (this.ie11) {
      // IE11: 旋轉角度
      this.$watch(() => this.settings.rotateAngle, (value) => {
        const ocx = this.$refs.ocx;
        const index = ocx.getDeviceIndex();
        ocx.setDeviceRotate(index, value);
      });
    }
  },

  mounted() {
    // 清空預設畫布大小
    const canvas = this.$refs.canvas;
    canvas.width = 0;
    canvas.height = 0;

    // 偵測視窗大小
    if (typeof ResizeObserver != 'undefined') {
      this.resizeObserver = new ResizeObserver(this.onResize);
      this.resizeObserver.observe(this.$el);
    } else {
      window.addEventListener('resize', this.onResize);
    }
  },

  beforeDestroy() {
    // 取消偵測視窗大小
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    } else {
      window.addEventListener('resize', this.onResize);
    }
  },

  methods: {
    async onMask(event) {
      // 設定遮罩
      /** @type {SetMaskRequest} */
      const req = {
        left: event.left,
        top: event.top,
        right: event.right,
        bottom: event.bottom,
      };

      await this.postWorkerMessage('setMask', req);
    },

    onResize() {
      // 更新樣式
      this.updateStyle();

      // 取消遮罩
      const maskVue = this.$refs.maskVue;
      maskVue.visible = false;
    },

    async initialize() {
      // IE11
      if (this.ie11) {
        this.initializeIE();
        return;
      }

      // 初始化背景執行緒
      if (!this.worker) {
        this.worker = new Worker('videoinput.worker.js');
        this.worker.onmessage = (e) => this.onWorkerMessage(e);
      }

      // 要求權限(打開不指定裝置再關閉)
      const reqPerm = async () => {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: true,
        });

        const tracks = stream.getTracks();
        tracks.forEach((track) => {
          track.stop();
        });
      };

      // 列舉裝置，如果裝置名稱為空值則先要求權限
      await this.listDevice();
      if (!this.devices.find((x) => !!x.name)) {
        await reqPerm();
        await this.listDevice();
      }

      // 預設裝置
      if (this.devices.length > 0) {
        this.device = this.devices[0];
      }
    },

    initializeIE() {
      // 列舉
      this.listDeviceIE();

      // 預設裝置
      if (this.devices.length > 0) {
        this.device = this.devices[0];
      }
    },

    async listDevice() {
      // IE11
      if (this.ie11) {
        this.listDeviceIE();
        return;
      }

      // 清空列表
      this.devices = [];

      // 列舉
      const list = await navigator.mediaDevices.enumerateDevices();
      for (let i = 0; i < list.length; i++) {
        const x = list[i];
        if (x.kind == 'videoinput') {
          this.devices.push({
            index: this.devices.length,
            name: x.label,
            path: x.deviceId,
          });
        }
      }
    },

    listDeviceIE() {
      // 清空列表
      this.devices = [];

      // 列舉
      const ocx = this.$refs.ocx;
      const count = ocx.GetDeviceCount();

      for (let i = 0; i < count; i++) {
        const name = ocx.getDeviceName(i);
        const path = ocx.getDevicePath(i);

        this.devices.push({
          index: this.devices.length,
          name: name,
          path: path,
        });
      }
    },

    /**
     * @param {VideoDevice} value
     */
    findDevice(value) {
      const index = value?.index;
      const name = value?.name;
      const path = value?.path;
      let found = null;

      if (!found && path) {
        found = this.devices.find((x) => (x.path == path));
      }

      if (!found && name) {
        found = this.devices.find((x) => (x.name.indexOf(name) >= 0));
      }

      if (!found && index >= 0 && index < this.devices.length) {
        found = this.devices[index];
      }

      return found;
    },

    /**
     * @param {VideoResolution} value
     */
    findResolution(value) {
      const index = value?.index;
      const width = value?.width;
      const height = value?.height;
      let found = null;

      if (!found && width > 0 && height > 0) {
        found = this.resolutions.find((x) => {
          return x.width == width && x.height == height;
        });
      }

      if (!found && index >= 0 && index <= this.resolutions.length) {
        found = this.resolutions[index];
      }

      return found;
    },

    async openDevice() {
      // IE11
      if (this.ie11) {
        this.openDeviceIE();
        return;
      }

      // 檢查參數
      if (!this.device) {
        throw new Error(`device is ${this.device}`);
      }

      // 關閉
      this.closeDevice();

      // 打開並播放
      const deviceId = this.device.path;
      const width = this.resolution?.width;
      const height = this.resolution?.height;

      this.stream = await navigator.mediaDevices.getUserMedia({
        video: {
          deviceId: { exact: deviceId },
          width: width > 0 ? { exact: width } : undefined,
          height: height > 0 ? { exact: height } : undefined,
        },
      });

      const video = this.$refs.video;
      video.srcObject = this.stream;
      await video.play();

      // 最大解析度
      let maxWidth = 0;
      let maxHeight = 0;

      const tracks = this.stream.getVideoTracks();
      tracks.forEach((x) => {
        try {
          const caps = x.getCapabilities();
          const w = caps.width.max;
          const h = caps.height.max;
          if (w >= maxWidth && h >= maxHeight) {
            maxWidth = w;
            maxHeight = h;
          }
        } catch (err) {
          console.error(err);
        }
      });

      // 清空列表
      this.resolutions = [];

      // 支援的解析度
      const list = [
        { width: 2592, height: 1944 },
        { width: 2048, height: 1536 },
        { width: 1920, height: 1080 },
        { width: 1600, height: 1200 },
        { width: 1280, height: 720 },
        { width: 1024, height: 768 },
        { width: 640, height: 480 },
        { width: 320, height: 240 },
      ];

      // 列舉解析度
      list.forEach((x) => {
        if (x.width <= maxWidth && x.height <= maxHeight) {
          this.resolutions.push({
            index: this.resolutions.length,
            width: x.width,
            height: x.height,
          });
        }
      });

      // 當前解析度
      let vidWidth = video.videoWidth;
      let vidHeight = video.videoHeight;

      if (vidWidth < vidHeight) {
        const temp = vidWidth;
        vidWidth = vidHeight;
        vidHeight = temp;
      }

      this.resolution = this.resolutions.find(
        (x) => (x.width == vidWidth && x.height == vidHeight));
      if (!this.resolution) {
        this.resolution = { width: vidWidth, height: vidHeight };
      }

      // 幀率
      let setFps = 0;
      tracks.forEach((x) => {
        try {
          const sets = x.getSettings();
          const fps = sets.frameRate;
          if (fps > 0) {
            setFps = fps;
            return;
          }
        } catch (err) {
          console.error(err);
        }
      });

      if (setFps == 0) {
        setFps = 15;
      }

      this.framerate = setFps;

      // 啟動運算迴圈
      setTimeout(() => {
        this.doLoop();
      }, 0);
    },

    openDeviceIE() {
      // 檢查參數
      if (!this.device) {
        throw new Error(`device is ${this.device}`);
      }

      // 關閉
      this.closeDeviceIE();

      // 打開
      const ocx = this.$refs.ocx;
      const index = this.device.index;

      if (!ocx.OpenDevice(index)) {
        throw new Error('ocx.OpenDevice');
      }

      // 設定解析度
      if (this.resolution) {
        const count = ocx.GetDeviceFormatCount(index);
        const format = this.resolution.index;
        const width = this.resolution.width;
        const height = this.resolution.height;

        if (format >= 0 && format <= count) {
          ocx.SetDeviceFormatIndex(index, format);
        } else if (width > 0 && height > 0) {
          ocx.SetDeviceFormat(index, width, height, '');
        }
      }

      // 播放
      if (!ocx.StartPlayDevice(index)) {
        throw new Error('ocx.StartPlayDevice');
      }

      // 清空列表
      this.resolutions = [];

      // 當前解析度
      const format = ocx.GetDeviceFormatIndex(index);
      let vidWidth = 0;
      let vidHeight = 0;

      // 列舉解析度
      const count = ocx.GetDeviceFormatCount(index);

      for (let i = 0; i < count; i++) {
        const text = ocx.GetDeviceFormatName(index, i);
        const sm = text.match(/(\d+) x (\d+), (\w+)/);
        if (sm) {
          const w = parseInt(sm[1]);
          const h = parseInt(sm[2]);
          const found = this.resolutions.find(
            (x) => (x.width == w && x.height == h));

          if (!found) {
            this.resolutions.push({
              index: this.resolutions.length,
              width: w,
              height: h,
            });
          }

          if (format == i) {
            vidWidth = w;
            vidHeight = h;
          }
        }
      }

      this.resolution = this.resolutions.find((x) => {
        return x.width == vidWidth && x.height == vidHeight;
      });
    },

    closeDevice() {
      // IE11
      if (this.ie11) {
        this.closeDeviceIE();
        return;
      }

      // 停止、關閉
      if (this.stream) {
        const tracks = this.stream.getTracks();
        tracks.forEach((x) => {
          x.stop();
        });

        this.stream = null;
      }

      const video = this.$refs.video;
      video.srcObject = null;
    },

    closeDeviceIE() {
      // 停止、關閉
      const ocx = this.$refs.ocx;
      const index = ocx.GetDeviceIndex();

      ocx.StopPlayDevice(index);
      ocx.CloseDevice(index);
    },

    doLoop() {
      const interval = 1000.0 / this.framerate;
      let t0 = performance.now();
      let n0 = 0;

      const loopOne = () => {
        // 停止播放時結束迴圈
        if (!this.stream) {
          return;
        }

        // 透過影格序號判斷是否為新畫面(Chrome version 80)
        const video = this.$refs.video;
        if (video.getVideoPlaybackQuality) {
          const vpq = video.getVideoPlaybackQuality();
          const n1 = vpq.totalVideoFrames;

          if (n1 > 0 && n1 <= n0) {
            setTimeout(loopOne, 1);
            return;
          }

          n0 = n1;
        }

        // 更新預覽畫面
        this.drawPreview();

        // 設定計時器
        const t1 = performance.now();
        t0 = Math.max(t0 + interval, t1 + 1);
        setTimeout(loopOne, t0 - t1);
      };
      loopOne();
    },

    drawPreview() {
      const rotate = this.settings.rotateAngle;
      const transpose = (rotate == 90 || rotate == 270);
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      const ww = video.videoWidth;
      const hh = video.videoHeight;
      const w1 = transpose ? hh : ww;
      const h1 = transpose ? ww : hh;

      // 切換解析度或旋轉
      if (canvas.width != w1 || canvas.height != h1) {
        canvas.width = w1;
        canvas.height = h1;
        this.onResize();
      }

      const ctx = canvas.getContext('2d');
      ctx.save();
      ctx.translate(w1 / 2, h1 / 2);
      ctx.rotate(rotate * Math.PI / 180);
      ctx.drawImage(video, - ww / 2, - hh / 2);
      ctx.restore();
    },

    updateStyle() {
      const container = this.$el;
      const canvas = this.$refs.canvas;
      const rect = container.getBoundingClientRect();
      const ow = rect.width;
      const oh = rect.height;
      const iw = canvas.width;
      const ih = canvas.height;

      // 畫布保持寬高比
      if (ow > 0 && oh > 0 && iw > 0 && ih > 0) {
        const fx = ow / iw;
        const fy = oh / ih;
        const fz = fx < fy ? fx : fy;
        const iw2 = iw * fz;
        const ih2 = ih * fz;

        const w = iw2 / ow;
        const h = ih2 / oh;
        const x = (1 - w) / 2;
        const y = (1 - h) / 2;

        this.containerStyle = {
          left: `${x * 100}%`,
          top: `${y * 100}%`,
          width: `${w * 100}%`,
          height: `${h * 100}%`,
        };
      } else {
        this.containerStyle = {
          display: 'none',
        };
      }
    },

    /**
     * @param {String} command
     * @param {Object} payload
     * @param {Object[]} buffers
     */
    async postWorkerMessage(command, payload, buffers) {
      return new Promise((resolve) => {
        const serial = ++this.workerSerial;
        this.workerQueue[serial] = (payload) => {
          resolve(payload);
        };

        /** @type {WorkerCommand} */
        const req = {
          command: command,
          serial: serial,
          payload: payload,
        };
        this.worker.postMessage(req, buffers);
      });
    },

    /**
     * @param {MessageEvent} e
     */
    onWorkerMessage(e) {
      /** @type {WorkerCommand} */
      const resp = e.data;
      const callback = this.workerQueue[resp.serial];

      if (callback) {
        callback(resp.payload);
      }
      delete this.workerQueue[resp.serial];
    },

    /** 
     * @param {String} ext
     */
    async takePicture(ext) {
      // IE11
      if (this.ie11) {
        return this.takePictureIE(ext);
      }

      // 檢查參數
      if (!ext) {
        throw new Error(`ext is ${ext}`);
      }

      const video = this.$refs.video;
      const canvas = document.createElement('canvas');
      const ww = video.videoWidth;
      const hh = video.videoHeight;

      canvas.width = ww;
      canvas.height = hh;

      const ctx = canvas.getContext('2d');
      ctx.drawImage(video, 0, 0);
      const imgd = ctx.getImageData(0, 0, ww, hh);

      // 影像處理
      /** @type {DoProcessRequest} */
      const req = {
        width: ww,
        height: hh,
        buffer: imgd.data.buffer,
        settings: this.settings,
      };
      await this.postWorkerMessage('doProcess', req, [req.buffer]);

      // 拍照
      const base64 = await this.postWorkerMessage('doGrab', ext);
      return base64;
    },

    /** 
     * @param {String} ext
     */
    takePictureIE(ext) {
      // 檢查參數
      if (!ext) {
        throw new Error(`ext is ${ext}`);
      }

      const ocx = this.$refs.ocx;
      ocx.SetColorMode(this.settings.colorMode);
      ocx.SetJpegQuality(this.settings.jpegQuality / 100.0);

      const base64 = ocx.GrabToBase64(ext);
      return base64;
    },

    /** 
     * @param {String} base64
     * @param {String} ext
     */
    async convertPicture(base64, ext) {
      /** @type {DoConvertRequest} */
      const req = {
        base64: base64,
        ext: ext,
      };
      base64 = await this.postWorkerMessage('doConvert', req);
      return base64;
    },
  },
};
</script>

<style lang="scss" scoped>
.videoinput-host {
  width: 100%;
  height: 100%;

  > .videoinput {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    > * {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    > video {
      display: none;
    }
  }
}
</style>
