<template>
  <div class="improc-host">
    <div class="improc">
      <div class="left-panel" />
      <div class="videoinput-panel">
        <VideoinputVue ref="videoinputVue" />
      </div>
      <div class="right-panel">
        <select v-model="deviceIndex">
          <option value="-1">關閉</option>
          <option
            v-for="device in devices"
            :key="device.index"
            :value="device.index"
          >
            {{ device.name }}
          </option>
        </select>
        <select v-model="resolutionIndex">
          <option
            v-for="resolution in resolutions"
            :key="resolution.index"
            :value="resolution.index"
          >
            {{ resolution.width }} x {{ resolution.height }}
          </option>
        </select>
        <select v-model="rotateAngle">
          <option value="0">0°</option>
          <option value="90">90°</option>
          <option value="180">180°</option>
          <option value="270">270°</option>
        </select>
      </div>
    </div>
  </div>
</template>

<script>
import VideoinputVue from './videoinput.vue';

export default {
  components: {
    VideoinputVue,
  },

  data() {
    return {
      devices: null,
      deviceIndex: -1,
      resolutions: null,
      resolutionIndex: -1,
      rotateAngle: 0,
    };
  },

  watch: {
    async deviceIndex() {
      const ocx = this.$refs.videoinputVue;
      ocx.device = ocx.findDevice({ index: this.deviceIndex });

      ocx.closeDevice();

      if (ocx.device) {
        await ocx.openDevice();
        this.resolutions = ocx.resolutions;
        this.resolutionIndex = ocx.resolution.index;
      }
    },

    async resolutionIndex() {
      const ocx = this.$refs.videoinputVue;
      ocx.resolution = ocx.findResolution({ index: this.resolutionIndex });

      await ocx.openDevice();
    },

    rotateAngle() {
      const ocx = this.$refs.videoinputVue;
      ocx.settings.rotateAngle = this.rotateAngle;
    },
  },

  async mounted() {
    const ocx = this.$refs.videoinputVue;
    await ocx.initialize();
    this.devices = ocx.devices;
  },

  methods: {
  },
};
</script>

<style lang="scss" scoped>
.improc-host {
  width: 100%;
  height: 100%;

  > .improc {
    background: white;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    width: 100%;
    height: 100%;

    > * {
      margin: 2.5px;
    }

    > .left-panel {
      border: 1px solid gray;
      display: none;
      flex-shrink: 0;
      width: 200px;
    }

    > .right-panel {
      flex-shrink: 0;
      width: 200px;
    }

    > .videoinput-panel {
      border: 1px solid gray;
      flex-grow: 1;
    }
  }

  select {
    max-width: 100%;
  }
}
</style>
