<template>
  <div
    class="mask"
    @pointerdown.capture="onPointerDown($event)"
    @pointerup.capture="onPointerUp($event)"
    @pointercancel.capture="onPointerUp($event)"
    @pointermove.capture="onPointerMove($event)"
    @touchstart.capture="onTouchStart($event)"
    @touchend.capture="onTouchEnd($event)"
    @touchcancel.capture="onTouchEnd($event)"
    @touchmove.capture="onTouchMove($event)"
  >
    <div v-if="visible">
      <div class="mask-border" nth="5" :style="borderStyle" />
      <div class="mask-vertex" nth="1" :style="vertexStyle[1]" />
      <div class="mask-vertex" nth="2" :style="vertexStyle[2]" />
      <div class="mask-vertex" nth="3" :style="vertexStyle[3]" />
      <div class="mask-vertex" nth="4" :style="vertexStyle[4]" />
      <div class="mask-vertex" nth="6" :style="vertexStyle[6]" />
      <div class="mask-vertex" nth="7" :style="vertexStyle[7]" />
      <div class="mask-vertex" nth="8" :style="vertexStyle[8]" />
      <div class="mask-vertex" nth="9" :style="vertexStyle[9]" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      left: 0,
      top: 0,
      right: 0,
      bottom: 0,
      lastButton: undefined,
      lastId: undefined,
      lastNth: 0,
      lastX: 0,
      lastY: 0,
      createnew: false,
      moving: false,
      visible: false,
      borderSize: 1.5,
      vertexSize: 10,
      borderStyle: {},
      vertexStyle: new Array(10),
    };
  },

  methods: {
    /** 開始遮罩 */
    onMaskBegin() {
      // 滑鼠點擊在空處(nth=0)時，建立遮罩
      if (this.lastNth == 0) {
        const rect = this.$el.getBoundingClientRect();
        this.left = (this.lastX - rect.left) / rect.width;
        this.top = (this.lastY - rect.top) / rect.height;
        this.right = this.left;
        this.bottom = this.top;
        this.lastNth = 9; // 選擇右下角頂點(nth=9)
        this.createnew = true;
        this.visible = true;
      }

      // 遮罩尚未移動
      this.moving = false;

      // 更新樣式
      this.updateStyle();
    },

    /** 停止遮罩 */
    onCropEnd() {
      const rect = this.$el.getBoundingClientRect();
      const vsize = this.vertexSize;
      const ww = (this.right - this.left) * rect.width;
      const hh = (this.bottom - this.top) * rect.height;
      let event;

      // 太小或沒有移動時，取消遮罩
      if (!this.moving || ww <= vsize || hh <= vsize) {
        this.left = 0;
        this.top = 0;
        this.right = 0;
        this.bottom = 0;
        this.visible = false;

        event = 'delete';
      } else if (this.createnew) {
        event = 'create';
      } else {
        event = 'modify';
      }

      // 遮罩已建立
      this.createnew = false;

      // 遮罩停止移動
      this.moving = false;

      // 更新樣式
      this.updateStyle();

      // 觸發事件
      this.$emit(event, {
        left: this.left,
        top: this.top,
        right: this.right,
        bottom: this.bottom,
      });
    },

    /** 移動遮罩
     * @param {Number} dx
     * @param {Number} dy
     */
    onCropMove(dx, dy) {
      const rect = this.$el.getBoundingClientRect();
      dx /= rect.width;
      dy /= rect.height;

      let x0 = this.left;
      let y0 = this.top;
      let x1 = this.right;
      let y1 = this.bottom;
      const w = this.right - this.left;
      const h = this.bottom - this.top;

      // 水平座標
      switch (this.lastNth) {
        case 5:
          x0 = Math.min(Math.max(x0 + dx, 0.0), 1.0 - w);
          x1 = x0 + w;
          break;
        case 1:
        case 4:
        case 7:
          x0 = Math.min(Math.max(x0 + dx, 0.0), 1.0);
          if (x0 > x1) {
            [x0, x1] = [x1, x0];
            this.lastNth += 2; // 1,4,7 -> 3,6,9
          }
          break;
        case 3:
        case 6:
        case 9:
          x1 = Math.min(Math.max(x1 + dx, 0.0), 1.0);
          if (x0 > x1) {
            [x0, x1] = [x1, x0];
            this.lastNth -= 2; // 3,6,9 -> 1,4,7
          }
          break;
      }

      // 垂直座標
      switch (this.lastNth) {
        case 5:
          y0 = Math.min(Math.max(y0 + dy, 0.0), 1.0 - h);
          y1 = y0 + h;
          break;
        case 1:
        case 2:
        case 3:
          y0 = Math.min(Math.max(y0 + dy, 0.0), 1.0);
          if (y0 > y1) {
            [y0, y1] = [y1, y0];
            this.lastNth += 6; // 1,2,3 -> 7,8,9
          }
          break;
        case 7:
        case 8:
        case 9:
          y1 = Math.min(Math.max(y1 + dy, 0.0), 1.0);
          if (y0 > y1) {
            [y0, y1] = [y1, y0];
            this.lastNth -= 6; // 7,8,9 -> 1,2,3
          }
          break;
      }

      this.left = x0;
      this.top = y0;
      this.right = x1;
      this.bottom = y1;

      // 遮罩正在移動
      this.moving = true;

      // 更新樣式
      this.updateStyle();
    },

    /**
     * @param {PointerEvent} event
     */
    onPointerDown(event) {
      event.preventDefault();

      if (this.lastId != undefined) {
        return;
      }

      if (event.button != 0/*left button*/) {
        return;
      }

      event.target.setPointerCapture(event.pointerId);
      this.lastButton = event.button;
      this.lastId = event.pointerId;
      this.lastNth = parseInt(event.target.getAttribute('nth')) || 0;
      this.lastX = event.clientX;
      this.lastY = event.clientY;
      this.onMaskBegin();
    },

    /**
     * @param {PointerEvent} event
     */
    onPointerUp(event) {
      event.preventDefault();

      if (event.pointerId != this.lastId) {
        return;
      }

      event.target.releasePointerCapture(event.pointerId);
      this.lastButton = undefined;
      this.lastId = undefined;
      this.lastNth = 0;
      this.onCropEnd();
    },

    /**
     * @param {PointerEvent} event
     */
    onPointerMove(event) {
      event.preventDefault();

      if (event.pointerId != this.lastId) {
        return;
      }

      const dx = event.clientX - this.lastX;
      const dy = event.clientY - this.lastY;
      this.lastX = event.clientX;
      this.lastY = event.clientY;
      this.onCropMove(dx, dy);
    },

    /**
     * @param {TouchEvent} event
     */
    onTouchStart(event) {
      event.preventDefault();

      if (this.lastId != undefined) {
        return;
      }

      const touch = event.touches[0];
      this.lastId = touch.identifier;
      this.lastNth = parseInt(event.target.getAttribute('nth')) || 0;
      this.lastX = touch.clientX;
      this.lastY = touch.clientY;
      this.onMaskBegin();
    },

    /**
     * @param {TouchEvent} event
     */
    onTouchEnd(event) {
      event.preventDefault();

      if (this.lastId == undefined) {
        return;
      }

      this.lastId = undefined;
      this.lastNth = 0;
      this.onCropEnd();
    },

    /**
     * @param {TouchEvent} event
     */
    onTouchMove(event) {
      event.preventDefault();

      const touch = event.touches[0];
      if (touch.identifier != this.lastId) {
        return;
      }

      const dx = touch.clientX - this.lastX;
      const dy = touch.clientY - this.lastY;
      this.lastX = touch.clientX;
      this.lastY = touch.clientY;
      this.onCropMove(dx, dy);
    },

    updateStyle() {
      const rect = this.$el.getBoundingClientRect();
      const bsize = this.borderSize;
      const xx = this.left * rect.width;
      const yy = this.top * rect.height;
      const ww = (this.right - this.left) * rect.width;
      const hh = (this.bottom - this.top) * rect.height;

      this.borderStyle = {
        left: `${Math.round(xx) - bsize}px`,
        top: `${Math.round(yy) - bsize}px`,
        width: `${Math.round(ww)}px`,
        height: `${Math.round(hh)}px`,
        'border-width': `${bsize}px`,
      };

      const vsize = this.vertexSize;
      const xlist = [xx, xx + ww / 2, xx + ww];
      const ylist = [yy, yy + hh / 2, yy + hh];

      for (let nth = 1; nth <= 9; nth++) {
        const left = xlist[parseInt((nth - 1) % 3)];
        const top = ylist[parseInt((nth - 1) / 3)];
        let visible = true;

        // 中間節點只在遮罩夠大時才顯示
        switch (nth) {
          case 2:
          case 8:
            visible = ww > (vsize * 10);
            break;
          case 4:
          case 6:
            visible = hh > (vsize * 10);
            break;
        }

        this.vertexStyle[nth] = {
          left: `${Math.round(left) - vsize / 2}px`,
          top: `${Math.round(top) - vsize / 2}px`,
          width: `${vsize}px`,
          height: `${vsize}px`,
          display: visible ? undefined : 'none',
        };
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.mask {
  position: relative;
  width: 100%;
  height: 100%;
  user-select: none;

  .mask-border {
    position: absolute;
    border: dashed red;
    cursor: move;
  }

  .mask-vertex {
    position: absolute;
    background: red;
  }

  $cursors: nw-resize, n-resize, ne-resize, w-resize, move, e-resize, sw-resize,
    s-resize, se-resize;
  @for $i from 1 through 9 {
    .mask-vertex[nth="#{$i}"] {
      cursor: nth($cursors, $i);
    }
  }
}
</style>
